<template>
  <div class="content">
    <div class="title">
      今日油价
    </div>
    <div class="contentB">
      <input class="input-text" type="text" placeholder="请输入城市" v-model="text">
      <br>
      <div class="button" @click="demand()">
        查询
      </div>
      <div class="show-oil" v-for="item in data">
        <h3>{{item.prov}}油价</h3>
        <div>
          0号柴油： {{item.p0}} <br>    
          89号汽油： {{item.p89}}<br> 
          90号汽油： {{item.p90}}<br> 
          92号汽油： {{item.p92}}<br> 
          93号汽油： {{item.p93}}<br> 
          95号汽油： {{item.p95}}<br> 
          97号汽油： {{item.p97}}<br> 
        </div>
        <div class="time">{{item.ct}}</div>
        
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    mounted(){
      
    },
    data(){
      return{
        apiUrl:"https://route.showapi.com/138-46",
        showapi_sign:"31fa47e38ce04aeba866b9624fe89ce8",
        showapi_appid:"39218",
        data:"",
        text:""
      }
    },
    created(){
      this.$store.dispatch("inOther");
    },
    methods:{
      demand(){
        let that = this;
        this.$http.post(
          this.apiUrl,
          {
            showapi_sign:this.showapi_sign,
            showapi_appid:this.showapi_appid,
            prov:this.text
          },
          {emulateJSON:true}
        ).then(
          (res) => {
            that.data = res.data.showapi_res_body.list
          }
        ),function (error) {

        }
      }
    }
  }
</script>
<style lang="less" scoped>
  li{
    list-style: none;
  }
  ul{
    margin: 0;
    padding: 0;
  }
  h3{
    margin-top: 0;
    margin-bottom: 10px;

  }
  .content{
    .contentB{
      .input-text{
        width: 30%;
      }
      .show-oil{
        width: 35%;
        background: #f6f8fa;
        padding: 20px 0;
        border-radius: 10px;
        margin: 20px auto;
        text-align: left;
        padding-left: 15%;
        line-height: 30px;
      }
      .time{
        color: #ccc;
        margin-top: 10px;
        margin-bottom: -10px;
      }
    }
  }
</style>



